import { registerPlugin } from '@capacitor/core';

import type { FFmpegKitPluginPlugin } from './definitions';

const FFmpegKitPlugin = registerPlugin<FFmpegKitPluginPlugin>(
  'FFmpegKitPlugin',
  {
    web: () => import('./web').then(m => new m.FFmpegKitPluginWeb()),
  },
);

export * from './definitions';
export { FFmpegKitPlugin };
